	module.exports = {"pathContext":{"frontmatter":{"path":"/docs/layout-direction","title":"Layout Direction","hasPlayground":false,"initialPlayground":null,"redirect":null},"html":"<h2>Layout Direction</h2>\n<p>Layout direction specifies the direction in which children and text\nin a hierarchy should be laid out. Layout direction also effects what\nedge <code>start</code> and <code>end</code> refer to. By default Yoga lays out with <code>LTR</code>\nlayout direction. In this mode <code>start</code> refers to <code>left</code> and <code>end</code>\nrefers to <code>right</code>. When localizing your apps for markets with RTL languages\nyou should customize this by either by passing a direction\nto the <code>CalculateLayout</code> call or by setting the direction on the root node.</p>\n<p><strong>LTR (DEFAULT)</strong> Text and children and laid our from left to right. Margin and\npadding applied the start of an element are applied on the left side.</p>\n<p><strong>RTL</strong> Text and children and laid our from right to left. Margin and\npadding applied the start of an element are applied on the right side.</p>\n<p><controls prop=\"layoutDirection\"></controls></p>"}}

